.mainDiv {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.mainDiv img {
    width: 7vw;
    left: 0;
    top : 0;
    position: absolute;
    padding: 2em;
    cursor: pointer;
}

.mainDiv h1 {
    font-size: 15vw;
    font-family: 'dirk';
    margin-bottom: -5%;
    margin-top: -10%;
    color: #e5e5da;
}

.mainDiv h2 {
    font-size: 5vw;
    font-family: 'FenwayPark';
}

.mainDiv h3 {
    font-size: 2.5vw;
    font-family: 'AvenirHeavy';
}

@media only screen and (max-width: 768px) {
    .mainDiv h1 {
    margin-top: 0;
    margin-bottom: -10%;
    font-size: 27vw;
    }
    
    .mainDiv h2 {
    margin-bottom: -10%;
    font-size: 10vw;
    }
    
    .mainDiv h3 {
        font-size: 5vw;
    }
    
    
    .mainDiv div img {
        width: 20%;
        margin-bottom: -10%;
        margin-right: -5%;
    }

    .mainDiv img {
        width: 25vw;
        left: 30vw;
    }
}